.btn {
  margin: 15px auto;
  color: green;
}
.text-center {
  text-align: center;
}
.text-right {
  float: right;
}
.body {
  margin: 0% 2% 1% 2%;
  color: white;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.pGame {
  color: greenyellow;
  font-size: large;
}
.imgGame {
  width: 65px;
  height: 60px;
}
.divGame {
  border: 5px ridge rgb(78, 225, 65);
  margin: 15px auto;
  padding: 10px;
  width: 200px;
  text-align: center;
}
.point {
  margin: 0px;
}
.game {
  text-align: center;
  margin: 20px;
  color: white;
}
.m-r-l {
  margin-right: 15px;
}
